.banner {
    position: relative;
    width: 100%;
    height: 400px; /* Set a fixed height for the banner */
    overflow: hidden;
    margin-top: 0.5rem; /* Converted 1rem to 1.6rem */
    border-radius: 0.2rem;
  
    &:hover .ChangeBanner {
      opacity: 1; /* Show buttons on hover */
      pointer-events: auto; /* Enable interaction */
    }
  }
  
  .bannerWrapper {
    display: flex;
    width: 100%;
    height: 100%;
    transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;; /* Smooth sliding animation */
  }
  
  .bannerImage {
    flex-shrink: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    background-color:#0b2e37;
  }
  
  .bannerDots {
    position: absolute;
    bottom: 1rem;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 0.5rem;
  }
  
  .dot {
    width: 1rem; /* Converted 10px to 1rem */
    height: 1rem; /* Converted 10px to 1rem */
    background-color: var(--gray-300);
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .dot.active {
    background-color: var(--primary); /* Highlight the active dot */
  }
  
  .ChangeBanner {
    position: absolute;
    top: 50%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    transform: translateY(-50%);
    opacity: 0; /* Hide buttons by default */
    pointer-events: none; /* Disable interaction when hidden */
    transition: opacity 0.3s ease-in-out; /* Smooth fade-in/out */
  }
  
  .prevButton,
  .nextButton {
    background-color: var(--primary);
    color: var(--white);
    border: none;
    padding: 0.8rem 1.6rem; /* Converted 0.5rem 1rem to 0.8rem 1.6rem */
    cursor: pointer;
    font-size: 2.4rem; /* Converted 1.5rem to 2.4rem */
    border-radius: 4px;
    transition: background-color 0.3s, transform 0.2s;
  
    &:hover {
      background-color: var(--primary-light);
      transform: scale(1.1);
    }
  }
  